import { useState } from 'react'
import { Table, Form, Button, ListGroup, ButtonGroup } from 'react-bootstrap'

const Blog = ({blog, addLikes, removeBlog}) => {
  const [visible, setVisible] = useState(false)

  const hideWhenVisible = { display: visible ? 'none' : '' }
  const showWhenVisible = { display: visible ? '' : 'none' }

  const toggleVisibility = () => {
    setVisible(!visible)
  }
  //console.log(blog.user.username)
    const blogStyle = {
        paddingTop: 10,
        paddingLeft: 2,
        border: 'solid',
        borderWidth: 1,
        marginBottom: 5
    }
    
   console.log(blog)
    return (
    <div style = {blogStyle}>
      <div style = {hideWhenVisible} className = 'blog'>
        <ListGroup>
        <ListGroup.Item> {blog.title}  {blog.author} </ListGroup.Item> {}
        <p> </p>
        </ListGroup>
        <Button variant="info" id = "view-button" onClick = {toggleVisibility}> View </Button>
        <p> </p>
      </div>
      <div style = {showWhenVisible} className = 'view'>
      <ListGroup>
      <ListGroup.Item> {blog.title}  {blog.author} </ListGroup.Item>
      </ListGroup>

      <ListGroup>
        <ListGroup.Item> {blog.url} </ListGroup.Item> 
        <ListGroup.Item> likes {blog.likes} </ListGroup.Item>  
      </ListGroup>
      <ListGroup>
        <ListGroup.Item> {blog.user.username}  </ListGroup.Item> 
          <br></br>
      </ListGroup>
      <ButtonGroup aria-label="Basic example">
          <Button variant="primary" id = "like-button" onClick = {() => addLikes(blog)}> Like </Button>
          <Button variant="secondary" onClick = {toggleVisibility}> Hide </Button>  
          { <Button variant="danger" id = "delete-button" onClick = {() => removeBlog(blog)}> Remove </Button>}
          <p>   </p>
      </ButtonGroup>
      <p> </p>
      </div>
    </div>
    )
}

export default Blog